
.movies{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 50px;
    align-items: flex-start;
    width: 80%;
    padding-top: 70px;
}

.movies .movie{
    width: 45%;
    background-color: white;
    margin-bottom: 70px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    padding: 20px;
    border-radius: 5px;
    color: gray;
    box-shadow: 10px 10px 5px 0px rgba(204,196,204,1);   
}

.movie img{
    position: relative;
    top: -50px;
    max-width: 120px;
    width: 100%;
    margin-right: 30px;
    
}

.movie h3,
.movie h5{
    font-weight: 300;
    margin: 0;

}

.movie .movie__data .movie__title{
    margin: 0%;
    margin-bottom: 5px;
    font-size: 23px;
    color: black;
    
    
}
.movie .movie__data .movie__year{
    margin: 0;
    margin-top: 15px;

}

.movie .movie__genres{
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    margin: 5px;
}

.movie__genres li{
    margin-right: 5px;
    font-size: 15px;
}